import Connection from '../services/connection';

const controllerName = 'auth';

class AuthController {

  static Login = body => {
    const result = Connection.POST(controllerName, 'login', body);
    return result;
  }

  static Register = body => {
    const result = Connection.POST(controllerName, 'register', body);
    return result;
  }

  static Email = query => {
    const result = Connection.POST(controllerName, 'email', query);
    return result;
  }

  static RestoreEmail = query => {
    const result = Connection.POST(controllerName, 'restore/email', query);
    return result;
  }

  static Restore = query => {
    const result = Connection.POST(controllerName, 'restore', query);
    return result;
  }

  static SocialLogin = query => {
    const result = Connection.POST(controllerName, 'socialLogin', query);
    return result;
  }

 

  // static EmailVerify = query => {
  //   const result = Connection.POST(controllerName, 'check', query);
  //   return result;
  // }

  // static Logout = () => {
  //   const result = Connection.GET(controllerName, 'Logout');
  //   return result;
  // }

  // static PasswordRecovery = query => {
  //   const result = Connection.GET(controllerName, 'PasswordRecoverySend', query);
  //   return result;
  // }

  // static PasswordRecoveryConfirm = body => {
  //   const result = Connection.POST(controllerName, 'PasswordRecoveryConfirm', body);
  //   return result;
  // }
}

export default AuthController;